*,
*::before,
*::after {
  border: 0;
  box-sizing: border-box;
  outline: none;
}

html {
  font-size: 0.9px;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16rem;
  font-weight: 400;
  color: #484e51;
  line-height: 1.5;
  word-spacing: 1px;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: none;
  color: #4dccbd;
  font-weight: 700;
}

a:hover {
  color: #3b9b90;
}

img {
  width: 100%;
  height: auto;
  -webkit-user-drag: none;
  user-select: none;
}

button {
  background: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  color: #303030;
  margin-top: 0;
  margin-bottom: 24rem;
}

h1 {
  font-size: 38rem;
  line-height: 1.2;
  letter-spacing: -1rem;
}

h2 {
  font-size: 35rem;
  line-height: 1.25;
  letter-spacing: -1rem;
}

h3 {
  font-size: 28rem;
  line-height: 1.3;
  letter-spacing: -1rem;
}

h4 {
  font-size: 24rem;
  line-height: 1.35;
  letter-spacing: -0.8rem;
}

h5 {
  font-size: 18rem;
  line-height: 1.5;
  letter-spacing: -0.5rem;
}

h6 {
  font-size: 15rem;
  line-height: 1.6;
  letter-spacing: 0;
}

/* ---------------------------------- Grid ---------------------------------- */

.container {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20rem;
}

@media (min-width: 480px) {
  .container {
    width: 85%;
    padding: 0;
  }
}

